
import {computed, nextTick, onBeforeUpdate, onMounted, reactive, watch} from "vue";
import {Store, useStore} from "vuex";

export default {
  name: "CMain",
  setup(){
    const store: Store<any> = useStore();
    const currenMediaWidth = computed(()=> store.state.currenMediaWidth);
    const data = reactive({
      carousel:{
        height: '600px',
        interval: 4000,
        srcList: [
          require('../../assets/images/server-carousel/1.jpg'),
          require('../../assets/images/server-carousel/2.jpg'),
          require('../../assets/images/server-carousel/3.jpg'),
          require('../../assets/images/server-carousel/4.jpg'),
          require('../../assets/images/server-carousel/5.jpg'),
          require('../../assets/images/server-carousel/6.jpg'),
          require('../../assets/images/server-carousel/7.jpg'),
          require('../../assets/images/server-carousel/8.jpg'),
        ]
      }
    });
    function updateCarouselMaxHeight(){
      let els = document.querySelectorAll('.server-carousel .el-carousel__item');
      let maxHeight = 0;
      let maxHeightLimit = 400;
      for (const el of els) {
        if(el.querySelector('img')!.height > maxHeight){
          maxHeight = el.querySelector('img')!.height;
        }
      }
      maxHeight = maxHeight > maxHeightLimit ? maxHeightLimit : maxHeight;
      data.carousel.height = maxHeight + 'px';
    }
    watch(currenMediaWidth, (nv, ov)=>{
      updateCarouselMaxHeight();
    });
    onMounted(()=>{
      updateCarouselMaxHeight();
      window.addEventListener('load', ()=>{
        updateCarouselMaxHeight();
      });
    });
    return {
      data,
    }
  }
}
