<script lang="ts">
import { defineComponent, reactive } from 'vue';

export default defineComponent({
  name: "CHeader",
  setup(){
    const data = reactive({
      menuItems: [
        {path: '#', title: '皮肤站'},
        {path: '#', title: '帮助'},
        {path: '#', title: '充值'},
        {path: '#', title: '加入我们'},
